.portfolio_container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.portfolio_item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.portfolio_item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
}

.portfolio_item_image_container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 280px;
}

.portfolio_item-image {
    transition: var(--transition);
}

.portfolio_item_vertical-image {
    width: calc(50% - 10px);
}

.portfolio_item_horizontal-image {
    width: calc(65% - 10px);
}

.portfolio_item-image:hover {
    transform: scale(1.1);
}

.portfolio_item a {
    color: var(--color-primary-variant);
    font-weight: 500;
    margin-top: 2%;
}

.portfolio_item h3:hover {
    color: var(--color-primary);
}

.portfolio-btn {
    margin-top: 1%;
    color: var(--color-bg);
    text-decoration: none;
    border-color: transparent;
}

.portfolio_item a:hover {
    color: var(--color-primary);
    background: transparent;
    border-color: var(--color-primary);
}


/* Media Queries for medium devices*/
@media screen and (max-width: 1024px) {
    .portfolio_container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .portfolio_container2 {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .pro3{
        width: auto;
        height: 250px;
    }

}



/* Media Queries for small devices*/
@media screen and (max-width: 600px) {
    .portfolio_container {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .portfolio_container2 {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .pro3{
        width: 250px;
        height: auto;
    }
   

}

@media screen and (min-width: 1920px) {
    .Portfolio h3 {
        font-size: 3rem;
    }

    .pro3{
        width: auto;
        height: 250px;
    }
}